.sidebar{
    display: flex;
    flex:0.35;
    flex-direction: column;
}
.sidebar__header{
    display:flex;
    padding:20px;
    border-right: 1px solid lightgray;
    justify-content: space-between;
}
.sidebar__right{
    display:flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar__right>.MuiSvgIcon-root{
    margin-right:2vw;
    font-size:24px !important;
}
.sidebar__search{
    display:flex;
    align-items: center;
    background-color: #f6f6f6;
    height:39px;
    padding:10px;
}
.sidebar__searchContainer{
    display:flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height:35px;
    border-radius:20px;
}
.sidebar__searchContainer>input{
    border:none;
    margin-left:10px;
}
.sidebar__searchContainer>.MuiSvgIcon-root{
    color:gray;
    padding:10px;
}
.sidebar__chats{
    flex:1;
    background-color: white;
    overflow:scroll;
}